@import url('https://fonts.googleapis.com/css2?family=Libre+Bodoni&family=Roboto+Mono&display=swap');
/* 
font-family: 'Libre Bodoni', serif;
font-family: 'Roboto Mono', monospace;
*/
*{
    padding: 0;
    margin: 0;
    border: 0;
}
.App{
    font-family: 'Libre Bodoni', serif;
}
.section{
    background-image: url('./imgs/background.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% auto;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* color: #F1F0EA; */
}
.Title{
    margin-top: 200px;
    font-size: 35px;
    /* background-color: #F1F0EA; */
    border: 2px black solid;
    border-radius: 20px;
    background-color: #d8e296;
    padding: 1em;
    height: min-content;
    box-shadow: rgba(0, 0, 0, 0.668) 0px 5px 15px;
}
.code{
    font-family: 'Roboto Mono', monospace;
    color: #CF5C36;
}
.DownArrow{
    
    position: absolute;
    animation: downArrow 1s infinite;
}
@keyframes downArrow {
    0%{
        bottom: 10px;
    }
    50%{
        bottom: 40px;
    }
    100%{
        bottom: 10px;
    }
}
.DownArrow > img{
    color: white;
    height: 100px;
}